body {
    font-family: 'Segoe UI', sans-serif;
    background-color: #121212;
    color: #fff;
    text-align: center;
    padding: 2rem;
  }
  
  .quiz-container {
    background: #1f1f1f;
    padding: 2rem;
    border-radius: 10px;
    max-width: 600px;
    margin: auto;
  }
  
  .options button {
    margin: 10px;
    padding: 10px 20px;
    background: #00adb5;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }
  
  .options button:hover {
    background: #007b83;
  }
  